.container {
    min-height: calc(110vh - 80px);
    background-color: white;
    clear: both;
    padding-top: 5vh;
    padding-bottom: 10vh;
}

.header_container {
    text-align: center;
}

.header_container > h1 {
    margin-top: 20px;
    letter-spacing: 1px;
    word-spacing: 9px;
    margin-bottom: 15vh;
}

#info_container {
    padding-left: 15vw;
    padding-right: 15vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 10vh;
}

#info_container > img {
    margin-top: 20px;
}

.box_description {
    line-height: 32px;
}

.box_header {
    margin-bottom: 0px;
}

.learn_button_container {
    width: 100%;
}
