#root {
    background-color: #f4f4f4;
    min-height: calc(50vh - 64px);
    padding: 10vh 10vw; 
}

.section_header {
    text-transform: uppercase;
    letter-spacing: 2.5px;
    color: #adadad;
    font-size: 12px;
    font-family: 'Inter', sans-serif;
    font-weight: 700;

}

.grid_row:first-of-type {
    margin-bottom: 10vh;
}

.contact_icon:not(:last-of-type) {
    margin-right: 2vw;
}

#email {
    color: #DB0202;
    font-size: 22px;
    font-weight: 500;
    text-decoration: none;
    font-family: 'Inter', sans-serif;
}

#email:hover {
    text-decoration: underline;
}

#info_header {
    margin-bottom: 25px;
}

.info_item {
    font-size: 14px;
    font-family: "Inter", sans-serif;
    color: #2D3436;
    pointer-events: all;
    text-decoration: none;
}

.info_item:hover {
    text-decoration: underline;
}


.grid_section > form {
    width: 90%;
}

#copyright_section {
    margin-top: 5vh;
    font-size: 14px;
    letter-spacing: 0.2px;
    line-height: 18px;
    font-family: "Inter", sans-serif;
}