h1 {
    font-size: 60px;
    font-weight: 900;
}

h2 {
    font-size: 32px;
}

h3 {
    font-size: 24px;
    font-weight: bold;
}

h4 {
    font-size: 18px;
    font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Inter', sans-serif;
    color: #2D3436;
}

.container {
    padding-top: 64px;
}
.page_1_container {
    min-height: calc(95vh - 80px - 64px);
    padding-left: 45px;
    padding-top: 80px;
    padding-bottom: 5vh;
}

.page_2_container {
    min-height: calc(105vh - 64px);
    clear: both;
    background-color: #F8F8F8;
    padding-top: 80px;
    padding-bottom: 10vh;
}


#student_img_container {
    float: right;
    width: 50%;
    margin-top: -5vh;
}

#student_img_container > img {
    width: 100%;
    height: auto;
    pointer-events: none;
}

.first_header, .second_header {
    
}


.first_header {
    width: 65vw;
}
.second_header {
    margin-top: 15vh;
    margin-bottom: 5vh;
    width: 40vw;
}

.button:first-of-type {
    margin-right: 1vw;
}

.button {
    
}

.button_container {
    display: flex;
    justify-content: center;
    width: 35vw;
}

.page_2_header_container {
    width: 45vw;
    text-align: center;
    margin-left: 27.5vw;
}

.info_header {
    color: #DB0202;
    font-size: 28px;
    margin-bottom: 5px;
    white-space: nowrap;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
}

.info:first-of-type {
    margin-right: 10vw;
}


.list_text {
    padding-top: 0px;
    margin: 0px;
    line-height: 32px;
}